#ResultField {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    height: 100%;
    padding-top: 170px;
    background-color: rgb(248, 255, 148);

    /* 隱藏ScrollBar並保留Scroll功能 */
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
        display: none;
    }
}

.ResultItem {
    display: flex;
    justify-content: space-around;

    background-color: white;
    width: 70%;
    margin-bottom: 10px;
    border-radius: 50px;
    box-shadow: 3px 3px 5px;

    user-select: none;

    transition: .2s;

    animation: {
        name: FadeInWithFallDown;
        duration: 1s;
    }

    &:hover {
        transform: scale(1.1);
    }
}

@media screen and (max-width: 1300px) {
    .ResultItem {
        width: 80%;
    }
}

@media screen and (max-width: 1150px) {
    .ResultItem {
        width: 90%;
    }
}

@keyframes FadeInWithFallDown {
    0% {
        opacity: 0;
        transform: translateY(-50%);
    }

    100% {
        opacity: 1;
        transform: translateY(0%);
    }
}

.Field {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 30%;

    margin: {
        top: 10px;
        bottom: 10px;
    }
}

.NumberField {
    align-self: center;

    width: 5%;
}

.SpaceField {
    width: 20%;
}

@media screen and (max-width: 1000px) {
    #ResultField {
        display: flex;
        flex-wrap: wrap;
        height: 100%;

        overflow-x: scroll;
    }

    .ResultItem {
        flex-direction: column;
        align-items: center;

        width: 40%;
        height: 70%;

        margin: {
            top: 30px;
            left: 10px;
            right: 10px;
        }
    }

    .Field {
        width: 100%;
    }
}

.Title {
    font: {
        size: 20px;
        weight: bold;
    }

    padding: 5px;
}

@media screen and (max-width: 700px) {
    .ResultItem {
        width: 50%;
    }
}

@media screen and (max-width: 550px) {
    .ResultItem {
        width: 55%;
    }
}

@media screen and (max-width: 500px) {
    .ResultItem {
        width: 60%;
    }
}

@media screen and (max-width: 450px) {
    .ResultItem {
        width: 70%;
    }
}

@media screen and (max-width: 400px) {
    .ResultItem {
        width: 80%;
    }
}

@media screen and (max-width: 350px) {
    .Title {
        font-size: 16px;
    }
}

.Road, .Space, .UpdateTime {
    padding-bottom: 5px;
}

#PaginationButtonGroup {
    display: flex;
    justify-content: center;

    width: 100%;
    background-color: rgb(243, 255, 77);
}

.PaginationButton {
    width: 100px;
    height: 40px;
    margin: 10px;
    font-size: 16px;
    background-color: #fff;
    border-radius: 50px;

    /* 處理Safari的預設style */
    -webkit-appearance: none;
    border: 2px black solid;

    &:hover {
        box-shadow: 3px 3px;

        animation: {
            name: ShakeItem;
            duration: .5s;
            iteration-count: infinite;
            delay: 2s;
        }
    }

    &:active {
        background-color: rgb(202, 202, 202);
        box-shadow: 3px 3px;
    }
}