#BlockContainer {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: rgba(255, 255, 255, 0.8);
    cursor: wait;
}

#Loader {
    border: 16px solid rgb(187, 187, 187);
    border: {
        top: 16px solid blue;
        radius: 50%;
    }

    width: 120px;
    height: 120px;

    animation: {
        name: UIBlockSpin;
        duration: 1.5s;
        iteration-count: infinite;
    }
}

@keyframes UIBlockSpin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

#Message {
    font: {
        size: 24px;
        weight: bold;
    }

    margin-top: 10px;

    user-select: none;
}